import React from "react";
import products from "./products.json";
import axios, { Axios } from "axios";
import { useState } from "react";
import { random } from "../Util";

var today = new Date();
var dd = String(today.getDate()).padStart(2, "0");
var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
let mo = parseInt(mm) - 1;
var yyyy = today.getFullYear();
const month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

today = dd + " " + month[mo] + " " + yyyy;

function Hero() {
     // Ref price
     let Data = 8;

     // Current Price
     const [currData, setcurrData] = useState("");

     // Random array number
     const [a, seta] = useState(random(products.length));

     // EGLD Api price
     let currPriceLink = `https://api.coinlore.net/api/ticker/?id=45467`;

     axios.get(currPriceLink).then((response) => {
          setcurrData(parseInt(response.data[0].price_usd));
     });

     function incr() {
          if (a === products.length - 1) {
               seta(0);
          } else {
               seta(a + 1);
          }
     }
     function decr() {
          if (a === 0) {
               seta(products.length - 1);
          } else {
               seta(a - 1);
          }
     }

     return (
          <div className="flex flex-row items-center justify-center h-screen text-xl text-center lg:space-y-1">
               <div className="flex flex-col lg:flex-row h-screen items-center justify-center">
                    <img src={`./images/${products[a].prodName}.png`} alt="" className="lg:w-96 w-44 md:w-60 lg:pt-12 pt-24 over" />
                    <div className="w-full h-screen flex items-center justify-center flex-col lg:p-3 lg:space-y-2 text lg:text-2xl text-base font-light px-4">
                         <p>If you have bought this {products[a].prodName} on late 2020</p>
                         <p>
                              It's price was around <em className="not-italic font-semibold text-blue-500">${products[a].relPrice.toLocaleString("en-US", { maximumFractionDigits: 2 })}</em> or {(products[a].relPrice / Data).toLocaleString("en-US", { maximumFractionDigits: 2 })} EGLD
                         </p>
                         <p>
                              Today, {today}, at the current EGLD price of ${currData}
                         </p>
                         <p>
                              Those {(products[a].relPrice / Data).toLocaleString("en-US", { maximumFractionDigits: 2 })} EGLD worth <em className="not-italic font-semibold text-blue-500">${((products[a].relPrice / Data).toFixed(2) * currData).toLocaleString("en-US", { maximumFractionDigits: 2 })}</em>
                         </p>
                         <p className="italic text-base py-3">{products[a].punch}</p>
                         <div className="lg:space-x-40 text-base space-x-20">
                              <button onClick={decr} className="bg-blue-700 p-2 px-5 rounded-lg hover:scale-105 transform transition-all duration-300">
                                   Prev
                              </button>
                              <button onClick={incr} className="bg-blue-700 p-2 px-5 rounded-lg hover:scale-105 transform transition-all duration-300">
                                   Next
                              </button>
                         </div>
                    </div>
               </div>
          </div>
     );
}

export default Hero;
